import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog.vue'
import uniIcons from '../../components/uni-icons/uni-icons.vue';
export default {
	components: {
		uniIcons,
		uniPopupDialog
	},
	data() {
		return {
			userList: [{
					icon: 'icon-08_zizhanghaoguanli',
					name: '账号管理',
					color: '#3DABFF',
					url: 'accountmange'
				},
				{
					icon: 'icon-fapiao',
					name: '开具发票',
					color: '#FF9C00',
					url: 'receipt'
				},
				{
					icon: 'icon-tianchongxing--',
					name: '账单记录',
					color: '#FFF000',
					url: 'bill'
				},
				{
					icon: 'icon-qianbao',
					name: '充值',
					color: '#2DC4B4',
					url: 'invest'
				},
				{
					icon: 'icon-juan',
					name: '现金劵',
					color: '#F8632F',
					url: 'cash'
				},
				{
					icon: 'icon-weixin',
					name: '微信改绑',
					color: '#26DD5B'
				}
			],
			is_bind: '',
			is_child: '',
			token: '',
			imgRemote: imgRemote,
			memberInfoData: '',
			member_default: '',
			code: ''
		};
	},
	methods: {
		memberInfo() {
			var that = this
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				timeStamp: timeStamp,
				sign: sign,
			}
			rs.getRequests("memberInfo", data, (res) => {
				// console.log(res)
				if (res.data.code == 200) {
					this.memberInfoData = res.data.data.info;
					this.member_default = res.data.data.member_default;
				}
			})
		},
		myinfoPage() {
			uni.navigateTo({
				url: "/pages/user/myinfo"
			})
		},
		pageUrl(item) {
			uni.navigateTo({
				url: `/pages/user/${item.url}`
			});
		},
		threePage(data) {
			if (!this.token) {
				switch (data) {
					case 'recomend':
						uni.navigateTo({
							url: '/pages/index/recomendlist'
						});
						break;
					case 'address':
						uni.navigateTo({
							url: '/pages/shopcart/delivery'
						});
						break;
					case 'password':
						uni.navigateTo({
							url: '/pages/user/modifypwd'
						});
						break;
				}
			} else {}
		},
		exit() {
			this.$refs.popup.open();
		},
		close() {
			this.$refs.popup.close();
		},
		confirm() {
			uni.showTabBar();
			uni.reLaunch({
				url: '/pages/account/login'
			});
		}
	},
	onLoad() {
		uni.hideTabBar();
	},
	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow: function() {
		var that = this;
		that.memberInfo();
		that.is_bind = uni.getStorageSync('is_bind');
		that.is_child = uni.getStorageSync("is_child");
		that.token = uni.getStorageSync("token");
	
	},
};
